import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  
  @Input() 
  public title: string = "";

  ngOnInit() {
    this.title = this.title ? this.title : "BBC";
  }
}
